import styled from 'styled-components';

export const Btn = styled.button`
  background: #ea5a5a;
  text-transform: uppercase;
  max-width: 160px;
  width: 100%;
  font-size: 14px;
  line-height: 2.5;
  border: none;
  cursor: pointer;
  color: #fff;
  font-family: Helvetica_Bold;
  border-radius: 20px;
  margin-left: 50%;
  transform: translateX(-50%);
`;